import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import ListGroup from 'react-bootstrap/ListGroup';
import Authenticated from '../../utils/authenticated';
import Background from '../styledComponents/background';
import Container from '../styledComponents/container';
import Content from '../styledComponents/content';
import {useEffect, useState} from 'react';
import { useNavigate } from "react-router-dom";
import Alert from 'react-bootstrap/Alert';
import Spinner from 'react-bootstrap/Spinner';
import api from '../../services/api';
import Header from '../../components/header';
import { useForm } from "react-hook-form";
import axios from 'axios';
import BingMapsReact from "bingmaps-react";
import Toolbox from "../styledComponents/toolbox";

const key = "Aj9CX9OQc4YL7RKoY5mGUwMb-rEveSGv7X1zYWajxtEkhL-fDnYE52DgoDeNCyob";

const AddLocalView = () => {
    const navigate = useNavigate();
    const { register, getValues, setValue } = useForm();
    const [validated, setValidated] = useState(false);
    const [sending, setSending] = useState(false);
    const [error, setError] = useState(null);
    const [address, setAddress] = useState(null);

    const handleSubmit = async(event) => {
      event.preventDefault();

      const form = event.currentTarget;
      const user = JSON.parse(localStorage.getItem('user'));
      if (form.checkValidity() === false) {
        event.stopPropagation();
        setValidated(true);
        return;
      }
      const formData = new FormData(form),
            formDataObj = Object.fromEntries(formData.entries())
      formDataObj.branch_id = user.member.branch_id;
      formDataObj.latitude = getValues("latitude")
      formDataObj.longitude = getValues("longitude");
      
      setSending(true);
      setError(false);

      await api.post("/locals", formDataObj, {
        headers: {
          "Content-Type": "multipart/form-data",
        }})
        .catch(e => {
          setError(e.message);
          setSending(false);
        })
        .then(res => {
          if (res.status == 200) {
            setSending(false);
            navigate('/locais');
          }
        })
    };

    let searchTimer = null;
    const handleAddLocalChange = async(e) => {
      if (searchTimer != null)
        clearTimeout(searchTimer);
      setAddress(null)
      searchTimer = setTimeout(async() => {
        const res = await axios.get(`http://dev.virtualearth.net/REST/v1/Locations`,
        {
          params: {
            query: e.target.value,
            include: "queryParse",
            key,
            output: "json"
          }
        });
        const results = res.data.resourceSets[0].resources;
        setAddress(results);
      }, 2000);
    }
    const handleAddress = (e, index) => {
      e.preventDefault()
      const adr = address[index];
      setValue("latitude", adr.point.coordinates[0]);
      setValue("longitude", adr.point.coordinates[1]);
      setValue("address", `${adr.address.addressLine}, ${adr.address.locality}`);
      setAddress(null);
    }

    useEffect(() => {
      register("latitude", -28.477940);
      register("longitude", -49.011889);
    }, []);

    return (
        <Authenticated>
          <Background>
            <Container>
              <Header route="/locais"/>
              <Toolbox>
                <h4>Criar local</h4>
              </Toolbox>
              <Content>
                <Form noValidate validated={validated} onSubmit={handleSubmit}>
                  <Form.Group className="mb-3">
                    <Form.Label>Nome</Form.Label>
                    <Form.Control required type="text" placeholder="Posto Trevo" {...register("name")}/>
                    <Form.Control.Feedback type="invalid">
                      D um nome ao local.
                    </Form.Control.Feedback>
                  </Form.Group>

                  <Form.Group className="mb-3">
                    <Form.Label>Endereo</Form.Label>
                    <Form.Control {...register("address")} required type="text" placeholder="Endereo" onChange={handleAddLocalChange}/>
                    <ListGroup>
                      {address == null ? <>Digite um endereo acima</>
                      :address.map((local, id) => (
                        <ListGroup.Item action onClick={(e) => handleAddress(e, id)}>
                         {local.name}
                        </ListGroup.Item>
                      ))}
                    </ListGroup>
                  </Form.Group>
                  {
                    getValues("latitude") && getValues("longitude")
                    ?<BingMapsReact
                      bingMapsKey={key}
                      height="300px"
                      mapOptions={{
                        navigationBarMode: "square",
                      }}
                      style={{
                        background: "gray"
                      }}
                      width="100%"
                      pushPins={[{
                          center: {
                            latitude: getValues("latitude"),
                            longitude: getValues("longitude")
                          },
                          options: {
                            title: getValues("name")
                          }
                        }
                      ]}
                      viewOptions={{
                        center: {
                          latitude: getValues("latitude"),
                          longitude: getValues("longitude")
                        },
                        mapTypeId: "grayscale",
                      }}
                    />
                    :<></>}
            
                  {error ? <Alert variant='danger'>{error}</Alert>:<></>}
                  {sending?<Button variant="primary" disabled>
                    <Spinner
                      as="span"
                      animation="border"
                      size="sm"
                      style={{marginRight: 10}}
                    />
                    Criando...
                  </Button>
                  :<Button variant="primary" type="submit">
                    Criar
                  </Button>}
                </Form>
              </Content>
            </Container>
          </Background>
        </Authenticated>
      );
}
export default AddLocalView;