import Authenticated from '../../utils/authenticated';
import Background from '../styledComponents/background';
import Container from '../styledComponents/container';
import Content from '../styledComponents/content';
import {useEffect, useState } from 'react';
import { useNavigate, useParams } from "react-router-dom";
import api from '../../services/api';
import bot from '../../services/bot';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Form from 'react-bootstrap/Form';
import Header from '../../components/header';
import styled from 'styled-components';
import Loader from '../styledComponents/loader';
import {IoIosClose} from 'react-icons/io';
import {BiBell, BiTrash, BiPencil, BiCheck, BiCalendar, BiPin} from 'react-icons/bi';
import {FiClock} from 'react-icons/fi';
import dateFormat from "dateformat";
import Toolbox from "../styledComponents/toolbox";

const StyledToolbox = styled(Toolbox)`
  h4 {
    width: 100%;
  }
`;
const InfoBox = styled.div`
  width: 100%;
  display: flex;
  margin-bottom: 20px;
`;

const Box = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  img {
    margin-left: auto;
    width: 150px;
    height: 150px;
    object-fit: contain;
  }
`;
const BoxImage = styled(Box)`
  justify-content: end;
`;

function ConfirmDeleteModal(props) {
  const [check, setCheck] = useState(false);
  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
        Ateno!
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {props.event ? <>
          <p>
            Voc est prestes a <b>deletar</b> o evento {props.event.name},
            isso inclui todo o histrico de participaes dos membros no mesmo.
            ao invs disso voc pode simplesmente desativa-lo, ele vai sumir da lista de eventos para os membros.
          </p>
          <Form.Check 
            value={check}
            onChange={e => setCheck(!check)}
            label="Li o texto e estou ciente da excluso do evento"
          />
        </>:<></>}
      </Modal.Body>
      <Modal.Footer>
        <Button disabled={!check} variant='danger' onClick={() => props.onConfirm(props.event)}>Excluir</Button>
        <Button onClick={() => props.onDesativate(props.event)}>Desativar</Button>
        <Button onClick={props.onHide}>Cancelar</Button>
      </Modal.Footer>
    </Modal>
  );
}
function ConfirmNotifyModal(props) {
  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
        Voc tem certeza disso?
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {props.event ? <>
          <p>
            Voc est prestes a notificar <b>todos</b> os membros da regio de {props.event.Branch.name} sobre o evento {props.event.name}.
            Tome cuidado com a quantidade de notificaes para no ser incmodo!
          </p>
        </>:<></>}
      </Modal.Body>
      <Modal.Footer>
        <Button variant='danger' onClick={() => props.onConfirm(props.event)}>Sim</Button>
        <Button onClick={props.onHide}>No</Button>
      </Modal.Footer>
    </Modal>
  );
}
function ConfirmNotifyCheckinModal(props) {
  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
        Voc tem certeza disso?
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {props.member ? <>
          <p>
            Voc est prestes a fazer o check-in de <b>{props.member.name}</b> no evento
          </p>
        </>:<></>}
      </Modal.Body>
      <Modal.Footer>
        <Button onClick={() => props.onConfirm(props.member.id)}>Fazer Check-in</Button>
        <Button onClick={props.onHide}>No fazer</Button>
      </Modal.Footer>
    </Modal>
  );
}

const EventView = () => {
  const navigate = useNavigate();
    const { eventId } = useParams();
    const [event, setEvent] = useState(null);
    const [participants, setParticipants] = useState([]);
    const [notifyModalShow, setNotifyModalShow] = useState(null);
    const [notifyCheckinModalShow, setNotifyCheckinModalShow] = useState(null);
    const [deleteModalShow, setDeleteModalShow] = useState(null);

    const getParticipants = async () => {
      await api.get("/events/" + eventId + "/participants",{ 
        headers: {
          "Content-Type": "multipart/form-data",
        }})
        .catch(e => {
          console.error(e.message);
        })
        .then(res => {
          if (res.status == 200) {
            setParticipants(res.data);
          }
        });
    }
    const getEvent = async () => {
      const user = JSON.parse(localStorage.getItem('user'));
      await api.get("/events/" + eventId,{ 
        headers: {
          "Content-Type": "multipart/form-data",
        }})
        .catch(e => {
          console.error(e.message);
        })
        .then(res => {
          if (res)
          if (res.status == 200) {
            setEvent(res.data);
            getParticipants();
          }
        });
    }
    const handleActive = async (isActive) => {
      await api.put("/events/" + event.id, {
        active: isActive
      }, {
        headers: {
          "Content-Type": "multipart/form-data",
        }})
        .catch(console.error);
      getEvent();
    }
    const handleEdit = () => {
      navigate("/eventos/editar/" + event.id);
    }
    const handleNotify = async () => {
      await bot.post(`/notify/${event.id}/all`);
      setNotifyModalShow(null);
    }
    const handleDelete = async () => {
      await api.delete("/events/" + event.id, {
        headers: {
          "Content-Type": "multipart/form-data",
        }})
        .catch(console.error);
      navigate("/eventos");
    }
    const handleDesactive = () => {
      handleActive(false);
      setDeleteModalShow(null);
    }
    const handleCheckin = async (memberId) => {
      await api.post("/events/" + eventId + "/checkin", {
        memberId: memberId
      });
      await bot.post("/notify/" + eventId + "/" + memberId + "/checkin", {
        memberId: memberId
      });
      getParticipants();
      setNotifyCheckinModalShow(null);
      alert("Check-in feito");
    }
    const handleCheckout = async (memberId) => {
      await api.post("/events/" + eventId + "/checkout", {
        memberId: memberId
      });
      getParticipants();
    }
    useEffect(() => {
      getEvent();
    }, []);
    return (
        <Authenticated>
          <Background>
            <Container style={{width: 700}}>
              <Header route="/eventos"/>
              {event != null ? <>
                  <StyledToolbox>
                    <h4>Evento {event.name}</h4>
                    Ativo
                    <Form.Check 
                      type="switch"
                      checked={event.active}
                      onChange={e => handleActive(event, e.target.checked)}
                    />
                    <Button variant='warning' onClick={e => setNotifyModalShow(event)}>
                      <BiBell/>
                    </Button>
                    <Button variant='warning' onClick={handleEdit}>
                      <BiPencil/>
                    </Button>
                    <Button variant='danger' onClick={e => setDeleteModalShow(event)}>
                      <BiTrash/>
                    </Button>
                  </StyledToolbox>
                  <Content>
                    <InfoBox>
                      <Box>
                        <h5>Informaes</h5>
                        <p><BiCalendar/> {dateFormat(event.date, "dd/mm/yyyy")}</p>
                        <p><FiClock/> {dateFormat(event.date, "HH:MM")}</p>
                        <p><BiPin/> {event.Local.name}</p>
                      </Box>
                      <BoxImage>
                        <img src={"http://192.168.0.100:3001/" + event.media_url}/>
                      </BoxImage>
                    </InfoBox>
                    <h5>Participantes</h5>
                    <Table striped bordered hover>
                    <thead>
                      <tr>
                        <th>Nome</th>
                        <th>Status</th>
                        <th>WhatsApp</th>
                        <th>Aes</th>
                      </tr>
                    </thead>
                    <tbody>
                      {participants.map(status => {
                        const getMessage = () => {
                          if (status.checkin)
                            return "Check-in feito";
                          if (status.confirmed)
                            return "Presena confirmada"
                          if (!status.confirmed)
                            return "Presena recusada"
                        }
                        return (
                          <tr>
                            <td>{status.Member.name}</td>
                            <td>{getMessage()}</td>
                            <td>
                              <a href={`https://wa.me/${status.Member.number}`}>
                                {status.Member.number}
                              </a>
                            </td>
                            <td>
                              {status.checkin
                              ?<Button variant='danger' onClick={(e) => handleCheckout(status.Member.id)}>
                                <IoIosClose/> Check-out
                              </Button>
                              :<Button variant='success' onClick={(e) => setNotifyCheckinModalShow(status.Member)}>
                                <BiCheck/> Check-in
                              </Button>}
                            </td>
                          </tr>
                        )
                      })}
                    </tbody>
                  </Table>
                </Content>
              </>:<Content>
                <Loader/>
              </Content>}
            </Container>
            <ConfirmNotifyModal
              show={notifyModalShow != null}
              event={notifyModalShow}
              onHide={() => setNotifyModalShow(null)}
              onConfirm={handleNotify}
            />
            <ConfirmNotifyCheckinModal
              show={notifyCheckinModalShow != null}
              member={notifyCheckinModalShow}
              onHide={() => setNotifyCheckinModalShow(null)}
              onConfirm={handleCheckin}
            />
            <ConfirmDeleteModal
              show={deleteModalShow != null}
              event={deleteModalShow}
              onHide={() => setDeleteModalShow(null)}
              onConfirm={handleDelete}
              onDesativate={handleDesactive}
            />
          </Background>
        </Authenticated>
      );
}
export default EventView;