import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import ListGroup from 'react-bootstrap/ListGroup';
import Authenticated from '../../utils/authenticated';
import Background from '../styledComponents/background';
import Container from '../styledComponents/container';
import Content from '../styledComponents/content';
import {useEffect, useState} from 'react';
import { useNavigate } from "react-router-dom";
import Alert from 'react-bootstrap/Alert';
import Spinner from 'react-bootstrap/Spinner';
import api from '../../services/api';
import Header from '../../components/header';
import { useForm } from "react-hook-form";
import Toolbox from "../styledComponents/toolbox";

const AddEventView = () => {
    const navigate = useNavigate();
    const { register, setValue } = useForm();
    const [locals, setLocals] = useState([]);
    const [validated, setValidated] = useState(false);
    const [sending, setSending] = useState(false);
    const [error, setError] = useState(null);

    const getLocals = async () => {
      const user = JSON.parse(localStorage.getItem('user'));
      const branchId = user.member.branch_id;
      const res = await api.get(`/branchs/${branchId}/locals`);
      setLocals(res.data);
    }
    const handleSubmit = async(event) => {
      const form = event.currentTarget;
      const user = JSON.parse(localStorage.getItem('user'));
      event.preventDefault();
      if (form.checkValidity() === false) {
        event.stopPropagation();
        setValidated(true);
        return;
      }
      const formData = new FormData(form),
            formDataObj = Object.fromEntries(formData.entries())
      formDataObj.date = new Date(`${formDataObj.date}T${formDataObj.time}`)
      formDataObj.branch_id = user.member.branch_id;

      setSending(true);
      setError(false);

      await api.post("/events", formDataObj, {
        headers: {
          "Content-Type": "multipart/form-data",
        }})
        .catch(e => {
          setError(e.message);
          setSending(false);
        })
        .then(res => {
          if (res.status == 200) {
            setSending(false);
            navigate('/eventos');
          }
        })
    };

    useEffect(() => {
      getLocals();
    }, []);

    const handleLocalSelect = (e) => {
      if(e.target.value == "new")
      {
        navigate('/locais/novo');
      }
    };

    return (
        <Authenticated>
          <Background>
            <Container>
              <Header route="/eventos"/>
              <Toolbox>
                <h4>Criar evento</h4>
              </Toolbox>
              <Content>
                <Form noValidate validated={validated} onSubmit={handleSubmit}>
                  <Form.Group className="mb-3">
                    <Form.Label>Nome</Form.Label>
                    <Form.Control required type="text" placeholder="Postinho" {...register("name")}/>
                    <Form.Control.Feedback type="invalid">
                      D um nome ao evento.
                    </Form.Control.Feedback>
                  </Form.Group>
                  <Row className="mb-3">
                    <Form.Group as={Col} controlId="formGridEmail">
                      <Form.Label>Data</Form.Label>
                      <Form.Control required type="date" {...register("date")}/>
                      <Form.Control.Feedback type="invalid">
                        Insira uma data.
                      </Form.Control.Feedback>
                    </Form.Group>

                    <Form.Group as={Col}>
                      <Form.Label>Hora</Form.Label>
                      <Form.Control required type="time" {...register("time")}/>
                      <Form.Control.Feedback type="invalid">
                        Insira uma hora.
                      </Form.Control.Feedback>
                    </Form.Group>
                  </Row>
                  <Form.Group className="mb-3">
                    <Form.Label>Local</Form.Label>
                    <Form.Select required {...register("local_id")} onChange={handleLocalSelect}>
                      <option value="">Selecione um</option>
                      {locals.map(local => <option value={local.id}>{local.name}</option>)}
                      <option value="new">Novo local</option>
                    </Form.Select>
                    <Form.Control.Feedback type="invalid">
                      Selecione um local.
                    </Form.Control.Feedback>
                  </Form.Group>            
                  <Form.Group className="mb-3">
                    <Form.Label>Arte</Form.Label>
                    <Form.Control type="file" {...register("file")}/>
                  </Form.Group>
                  {error ? <Alert variant='danger'>{error}</Alert>:<></>}
                  {sending?<Button variant="primary" disabled>
                    <Spinner
                      as="span"
                      animation="border"
                      size="sm"
                      style={{marginRight: 10}}
                    />
                    Criando...
                  </Button>
                  :<Button variant="primary" type="submit">
                    Criar
                  </Button>}
                </Form>
              </Content>
            </Container>
          </Background>
        </Authenticated>
      );
}
export default AddEventView;