import ReactDOM from "react-dom/client";
import {
  BrowserRouter,
  Routes,
  Route
} from "react-router-dom";
import 'bootstrap/dist/css/bootstrap.min.css';
import './app.scss';

//Events
import AddEventView from "./views/events/add";
import EditEventView from "./views/events/edit";
import EventView from "./views/events/event";
import EventsView from "./views/events";
//Locals
import AddLocalView from "./views/locals/add";
import EditLocalView from "./views/locals/edit";
import LocalsView from "./views/locals";

import MainView from "./views/main";
import LoginView from "./views/auth/login";

const root = ReactDOM.createRoot(
  document.getElementById("root")
);
root.render(
  <BrowserRouter>
    <Routes>
      <Route path="/login" element={<LoginView />}/>

      <Route path="/eventos/novo" element={<AddEventView />}/>
      <Route path="/eventos/editar/:eventId" element={<EditEventView />}/>
      <Route path="/eventos/:eventId" element={<EventView />}/>
      <Route path="/eventos" element={<EventsView />}/>

      <Route path="/locais/novo" element={<AddLocalView />}/>
      <Route path="/locais/:localId" element={<EditLocalView />}/>
      <Route path="/locais" element={<LocalsView />}/>

      <Route path="/" element={<MainView />}/>
    </Routes>
  </BrowserRouter>
);