import Authenticated from '../../utils/authenticated';
import Background from '../styledComponents/background';
import Container from '../styledComponents/container';
import Content from '../styledComponents/content';
import {useEffect, useState} from 'react';
import { useNavigate } from "react-router-dom";
import api from '../../services/api';
import bot from '../../services/bot';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Form from 'react-bootstrap/Form';
import dateFormat from "dateformat";
import Header from '../../components/header';
import styled from 'styled-components';
import Loader from '../styledComponents/loader';
import {IoMdAdd} from 'react-icons/io';


const Toolbox = styled.div`
  padding: 20px;
  background-color: #ecf0f1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  gap: 5px;
  h4 {
  }
`;

const EventsView = () => {
  const navigate = useNavigate();
    const [events, setEvents] = useState([]);
    const [loading, setLoading] = useState(true);

    const getEvents = async () => {
      const user = JSON.parse(localStorage.getItem('user'));
      const branchId = user.member.branch_id;
      const resBranch = await api.get(`/branchs/${branchId}/events`);
      const resMain = await api.get(`/branchs/main/events`);
      setEvents([...resBranch.data, ...resMain.data]);
      setLoading(false);
    }
    useEffect(() => {
      getEvents();
    }, []);
    return (
        <Authenticated>
          <Background>
            <Container style={{width: 700}}>
              <Header route="/"/>
              <Toolbox>
                <h4>Eventos</h4>
                <Button variant='success' onClick={e => navigate("/eventos/novo")}>
                  <IoMdAdd/> Criar evento
                </Button>
              </Toolbox>
              <Content>
                  <Table striped bordered hover>
                  <thead>
                    <tr>
                      <th>Nome</th>
                      <th>Data</th>
                      <th>Local</th>
                      <th>Status</th>
                    </tr>
                  </thead>
                  <tbody>
                    {events.map(event => {
                      const date = dateFormat(event.date, "dd/mm/yyyy HH:MM");
                      return (<tr>
                        <td>
                          {event.is_sympla ? 
                          <a href={event.sympla_link}>
                            {event.name} (Sympla)
                          </a>
                          :<a href="" onClick={(e) => navigate("/eventos/" + event.id)}>
                            {event.name}
                          </a>}
                        </td>
                        <td>{date}</td>
                        <td>{event.Local.name}</td>
                        <td>
                          {event.is_sympla || event.active ? "Ativo" : "Desativado"}
                        </td>
                      </tr>)
                    })}
                  </tbody>
                </Table>
                {loading?<Loader/>:<>
                </>}
              </Content>
            </Container>
          </Background>
        </Authenticated>
      );
}
export default EventsView;