import Alert from 'react-bootstrap/Alert';
import Background from './styledComponents/background';
import Container from './styledComponents/container';
import Header from '../components/header';
import styled from 'styled-components';
import { useNavigate } from "react-router-dom";
import Authenticated from '../utils/authenticated';
import eventButtonBackground from "../assets/events.png";
import localButtonBackground from "../assets/locals.png";
import memberButtonBackground from "../assets/members.png";

const List = styled.div`
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 5px;
    padding: 5px;
`; 
const CustomButton = styled.button`
    border: none;
    outline: none;
    background-image: url('${props => props.src}');
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 5px;
    display: flex;
    justify-content: flex-start;
    align-items: end;
    padding: 10px;
    width: 100%;
    height: 200px;
    color: #fff;
    filter: grayscale(100%);
    cursor: pointer;
    transition: filter ease .2s;

    &:hover {
        filter: grayscale(0%);
    }
`;

const MainView = () => {
    const navigate = useNavigate();
    return (
        <Authenticated>
            <Background>
                <Container>
                    <Header/>
                    <List>
                        <Alert variant='info' style={{margin: 0}}>
                            Teste de alerta para os ADMs
                        </Alert>
                        <CustomButton onClick={e => navigate("/eventos")} src={eventButtonBackground}>
                            Eventos
                        </CustomButton>
                        <CustomButton onClick={e => navigate("/locais")} src={localButtonBackground}>
                            Locais
                        </CustomButton>
                        <CustomButton onClick={e => navigate("/membros")} src={memberButtonBackground}>
                            Membros
                        </CustomButton>
                    </List>
                </Container>
            </Background>
        </Authenticated>
      );
}
export default MainView;