import Authenticated from '../../utils/authenticated';
import Background from '../styledComponents/background';
import Container from '../styledComponents/container';
import Content from '../styledComponents/content';
import {useEffect, useState} from 'react';
import { useNavigate } from "react-router-dom";
import api from '../../services/api';
import Table from 'react-bootstrap/Table';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import {BiTrash,BiPencil} from 'react-icons/bi';
import {IoMdAdd} from 'react-icons/io';
import Header from '../../components/header';
import Toolbox from "../styledComponents/toolbox";

function ConfirmDeleteModal(props) {
  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
        Voc tem certeza disso?
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {props.local ? <>
          <p>
            Voc est prestes a <b>deletar</b> o local {props.local.name}.
          </p>
        </>:<></>}
      </Modal.Body>
      <Modal.Footer>
        <Button variant='danger' onClick={() => props.onConfirm(props.local)}>Deletar</Button>
        <Button onClick={props.onHide}>No</Button>
      </Modal.Footer>
    </Modal>
  );
}
function ErrorModal(props) {
  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
        Ops!
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        {props.message}
      </Modal.Body>
      <Modal.Footer>
        <Button onClick={props.onHide}>Ok</Button>
      </Modal.Footer>
    </Modal>
  );
}

const LocalsView = () => {
  const navigate = useNavigate();
    const [locals, setLocals] = useState([]);
    const [deleteConfirmModalShow, setDeleteConfirmModalShow] = useState(null);
    const [errorModalShow, setErrorModalShow] = useState(null);
    const getLocals = async () => {
      const user = JSON.parse(localStorage.getItem('user'));
      const branchId = user.member.branch_id;
      const resBranch = await api.get(`/branchs/${branchId}/locals`);
      setLocals(resBranch.data);
    }
    const handleEdit = (local) => {
      navigate("/locais/" + local.id);
    }
    const handleDelete = async (local) => {
      setDeleteConfirmModalShow(null);
      await api.delete("/locals/" + local.id)
        .catch(err => {
          setErrorModalShow("No foi possvel deletar esse local, pois existe um evento cadastrado com esta localizao.");
        })
      getLocals();
    }
    useEffect(() => {
      getLocals();
    }, []);
    return (
        <Authenticated>
          <Background>
            <Container style={{width: 700}}>
              <Header route="/"/>
              <Toolbox>
                <h4>Locais</h4>
                <Button variant="success" onClick={e => navigate("/locais/novo")}>
                  <IoMdAdd/> Criar local
                </Button>
              </Toolbox>
              <Content>
                  <Table striped bordered hover>
                  <thead>
                    <tr>
                      <th>Nome</th>
                      <th>Endereo</th>
                      <th>Aes</th>
                    </tr>
                  </thead>
                  <tbody>
                    {locals.map(local => {
                      return (<tr>
                        <td>{local.name}</td>
                        <td>{local.address}</td>
                        <td style={{display:'flex', gap: 5}}>
                          <Button onClick={e => handleEdit(local)}>
                            <BiPencil/>
                          </Button>
                          <Button onClick={e => setDeleteConfirmModalShow(local)} variant='danger'>
                            <BiTrash/>
                          </Button>
                        </td>
                      </tr>)
                    })}
                  </tbody>
                </Table>
              </Content>
            </Container>
            <ConfirmDeleteModal
              show={deleteConfirmModalShow != null}
              local={deleteConfirmModalShow}
              onHide={() => setDeleteConfirmModalShow(null)}
              onConfirm={handleDelete}
            />
            <ErrorModal
              show={errorModalShow != null}
              message={errorModalShow}
              onHide={() => setErrorModalShow(null)}
            />
          </Background>
        </Authenticated>
      );
}
export default LocalsView;